.login-title {
  font-size: 40px;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  line-height: 48px;
  margin-top: 60px;
  padding-bottom: 60px;
  border-bottom: 1px solid #dedddc;
}

.form-wrapper {
  margin: 0 auto;
  width: 40%;
}

.form-input-title {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.135px;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 8px;
}

.btn-login {
  float: right;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 1.25px;
  text-transform: uppercase;

  background: #6200ee;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.14), 0px 1px 10px rgba(0, 0, 0, 0.12),
    0px 2px 4px rgba(0, 0, 0, 0.2);
  border-radius: 50px;
  padding: 16px 38px;
}

.btn-register {
  margin-right: 24px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  /* identical to box height, or 133% */

  letter-spacing: 0.135px;

  color: rgba(21, 42, 235, 0.8);
}

.btn-register a {
  text-decoration: none;
}

.icon-facebook {
  font-size: 40px;
  color: #fff;
}

.facebook-register {
  background: #1877f2;
  box-shadow: 0px 38.4869px 71.4756px rgba(0, 0, 0, 0.07);
  border-radius: 20.9968px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 4%;
}

.facebook-register span {
  color: white;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
}
